html {
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;

  --color-pink: #ff3cd2;
  --color-black: #151515;
  --color-white: #fff;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-white);
  min-height: 200vh;
  overflow-x: hidden;
}

.main {
  max-width: calc(1280px + 2em);
  margin: 0 auto;
  padding: 1em 2em;
  font-size: 1.4em;
  line-height: 1.4em;
}
